<template>
    <b-container style="margin-top:30px">
    <b-progress v-if="samples_num > 0" :value="index+1" :max="samples_num" show-progress animated></b-progress>

    <b-container style="margin-top: 30px; margin-bottom: 30px; display: flex; flex-wrap: nowrap; justify-content: center; flex-direction: row; align-items: center;">
        <b-button-group style="margin-right: 30px">
            <b-button variant="success" @click="prev()">上一个</b-button>
            <b-button variant="warning" @click="next()">下一个</b-button>
        </b-button-group>
        <div>
            <b-badge href="#" variant="secondary">索引 {{index}}</b-badge>
        </div>
    </b-container>
    
    <b-container>
        <b-row style="margin-bottom: 20px">
            <!-- variable name -->
            <b-col v-for="(sample, sample_i) in samples" :key="sample_i" style="display: flex;justify-content: center;">
                {{sample['title']}}
            </b-col>
        </b-row>
        <b-row style="margin-bottom: 20px">
            <!-- variable -->
            <b-col v-for="(sample, sample_i) in samples" :key="sample_i" style="display: flex;justify-content: center;">
                <div v-if="sample['type'] == 'IMAGE'">
                    <img :src="sample['data']" :width="sample['width']" :height="sample['height']"/>
                </div>
                <div v-else>
                    {{sample['data']}}
                </div>
            </b-col>
        </b-row>
        <b-row style="margin-bottom: 20px">
            <!-- variable tages -->
            <b-col v-for="(sample, sample_i) in samples" :key="sample_i" style="display: flex;justify-content: center;">
                <b-form-group>
                <b-form-checkbox-group v-model="sample['tag']">
                    <b-form-checkbox :value="tag" v-for="(tag, tag_i) in tags" :key="tag_i">{{tag}}</b-form-checkbox>
                </b-form-checkbox-group>
                </b-form-group>
            </b-col>
        </b-row>
    </b-container>

    </b-container>
</template>

<script>
export default{
    name: 'Browser',
    data(){
        return {
            samples_num: 0,
            index: 0,
            samples:[],
            tags:[]
        }
    },
    mounted: function(){
        var _this = this;
        _this.axios.get('/antgo/api/browser/sample/entry/').then(function(res){
            _this.samples = res.data.content['value'];
            _this.index = res.data.content['step'];
            _this.tags = res.data.content['tags'];
            _this.samples_num = res.data.content['samples_num'];
        }).catch(function(error){
            // do nothing
            console.log(error)
            _this.index = -1;
        })
    },
    methods:{
        prev: function(){
            var _this = this;
            _this.axios.get('/antgo/api/browser/sample/prev/', {
                params:{
                    'step': _this.index,
                    'data': JSON.stringify(_this.samples)           
                }
            }).then(function(res){
                _this.samples = res.data.content['value'];
                _this.index = res.data.content['step'];
                _this.tags = res.data.content['tags']
            }).catch(function(error){
                // do nothing
                console.log(error)
            })
        },
        next: function(){
            var _this = this;
            _this.axios.get('/antgo/api/browser/sample/next/', {
                params:{
                    'step': _this.index,
                    'data': JSON.stringify(_this.samples)
                }
            }).then(function(res){
                _this.samples = res.data.content['value'];
                _this.index = res.data.content['step'];
                _this.tags = res.data.content['tags']
            }).catch(function(error){
                // do nothing
                console.log(error)
            })
        }
    }
}
</script>

